 
import { VhOrderInvoice, VhOrderInvoiceDetail ,VhOrderInvoices} from './../../../../../../../interface/vh-order-invoice';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {   VhAuth, VhAlgorithm, VhQueryCafe, VhQuery } from 'ionic-vhframeworks';
import { SalesA45aComponent } from 'src/app/cafe/components/print/sales/A45/sales-a45a/sales-a45a.component';
import { SalesK57aComponent } from 'src/app/cafe/components/print/sales/K57/sales-k57a/sales-k57a.component';
import { SalesK80aComponent } from 'src/app/cafe/components/print/sales/K80/sales-k80a/sales-k80a.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component'; 
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-sales',
  templateUrl: './sales.component.html',
  styleUrls: ['./sales.component.scss']
})
export class SalesComponent {

  tableHeight: string;
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  public invoiceInfo: any;
  public invoice: VhOrderInvoices
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private vhQuery:VhQuery
  ) {
    this.getDataInvoice(this.router.getCurrentNavigation().extras.state.id)
    console.log(this.dataRestore);
    this.init()
  }

  init(invoice: VhOrderInvoice = null, invoiceDetail: VhOrderInvoiceDetail[] = null) {
    this.invoice = new VhOrderInvoices(invoice, invoiceDetail)
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee()),
        partner: this.vhQueryCafe.getlocalCustomer(this.invoice.getIdPartner()),
        paymentName: this.setPaymentName(this.invoice.getPaymentType())
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        let invoiceDetailFresh: VhOrderInvoiceDetail[] = invoiceDetail.map(item => {
          if (item.combos) {
            let combo: any = {
              ...item,
              ... this.getNameProduct(item.id_product, item.id_subproduct),
              combos: item.combos.map(element => {
                return { ...element, ... this.getNameProduct(element.id_product, element.id_subproduct) }
              })
            }

            return { ...combo, subComboName: this.getSubCombo(combo.combos) }
          } else
            return {
              ...item,
              ... this.getNameProduct(item.id_product, item.id_subproduct)
            }
        })
        this.init(invoice, invoiceDetailFresh)
        this.vhComponent.hideLoading(0)
      })
    })
  }
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQueryCafe.getlocalWallet(this.invoice.getIdWallet()).name
    }
  }
  getNameProduct(id_prod: string, id_subProd: string) {
    let product = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any
    if (id_subProd) subFood = product.subs.find(item => item._id == id_subProd)
    return { name: `${product ? product.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`, unit: product.unit }
  }
  /**Render cc thnh phn combo */
  getSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getlocalPrinters().find(item => item.name == "DESKTOP")
      if (printer && printer.sales.enable) resolve(printer.sales)
      else rejects("No")
    })
  }

  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['type']), { printer, invoice: this.invoice }, false, false, `modal-print-${printer['type']}`)
    }, (err) => {
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/cafe/dashboard/management/printer']))
    })
  }

  private renderPrintPage(type: 'K57' | 'K80' | 'A4') {
    switch (type) {
      case 'K57': return SalesK57aComponent
      case 'K80': return SalesK80aComponent
      default: return SalesA45aComponent
    }
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/cashflow/money-control'], { state: this.dataRestore });
  }
  ngAfterViewChecked() {
    if (document.getElementById('sales-invoice-detail-today') && document.querySelector(".sales-cart-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.getElementById('sales-invoice-detail-today').clientHeight - document.querySelector(".sales-cart-header").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }

}
