import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm,  VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ReceiveMoneyA45Component } from 'src/app/cafe/components/print/receive-money/A45/receive-money-a45/receive-money-a45.component';
import { ReceiveMoneyK57Component } from 'src/app/cafe/components/print/receive-money/K57/receive-money-k57/receive-money-k57.component';
import { ReceiveMoneyK80Component } from 'src/app/cafe/components/print/receive-money/K80/receive-money-k80/receive-money-k80.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-transfer-receive',
  templateUrl: './transfer-receive.component.html',
  styleUrls: ['./transfer-receive.component.scss']
})
export class TransferReceiveComponent implements OnInit {

  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public lang: LanguageService,
  ) {
  }
  checkPrinter: boolean = false
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  billDetail: any = this.router.getCurrentNavigation().extras.state.invoice
  nzOptions: any;
  ngOnInit(): void {
    let printer = this.vhQueryCafe.getlocalPrinters().find(item => item.name == "DESKTOP")
    if (printer) {
      if (printer.delivery_receive_money) this.checkPrinter = printer.delivery_receive_money.enable
    }
    this.getPaymentBankList();
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/cashflow/money-control'], { state: this.dataRestore });
  }
  /**Get payment bank list */
  payment_method: Array<any> = []
  getPaymentBankList() {
    let listPayment = this.vhQueryCafe.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
    let listPaymentBank = listPayment.map(item => {
      return { value: item._id, label: item.name, isLeaf: true }
    })
    let options = [{ value: '1', label: this.lang.translate('Cash'), isLeaf: true }, { value: '3', label: this.lang.translate('Account'), children: listPaymentBank }]
    this.nzOptions = options;
    if (this.billDetail['bill_type'] == 33) this.billDetail['branch_name'] = this.vhQueryCafe.getlocalBranch(this.billDetail['id_branch_receive']).name
    else this.billDetail['branch_name'] = this.vhQueryCafe.getlocalBranch(this.billDetail['id_branch_send']).name
    console.log(this.billDetail);
  }
  showPrint(goback?) {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.vhComponent.showModal(this.renderPrintPage(printer['type']), {
          billInfo: {
            ...this.billDetail,
            id_branch_send: this.billDetail['id_branch'],
            id_employee: this.vhAuth.getUser()._id
          },
          payment_type: +this.payment_method[0],
          id_wallet: +this.payment_method[0] == 1 ? 'id_cash' : this.payment_method[1],
          printer: printer
        }, false, false, `modal-print-${printer['type']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { if (goback) this.goBack() })
          })
      }
    }, err => { this.goBack() })
  }

  private renderPrintPage(type: 'K57' | 'K80' | 'A4') {
    switch (type) {
      case 'A4': return ReceiveMoneyA45Component;
      case 'K57': return ReceiveMoneyK57Component;
      case 'K80': return ReceiveMoneyK80Component;
    }
  }
  /*----------Print------------------*/
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getlocalPrinters().find(item => item.name == "DESKTOP")
      if (printer && printer.delivery_receive_money.enable) resolve(printer.delivery_receive_money)
      else rejects("No")
    })
  }
  checkBillType(bill_type) {
    //kim tra loi ha n
    switch (bill_type) {
      case 33: {
        return (this.lang.translate("Money transfer receipt"));
      }
      case 34: {
        return (this.lang.translate("Money received receipt"));
      }
    }
  }
}
