import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm,  VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ExpensesA4Component } from 'src/app/cafe/components/print/expenses/A45/expenses-a4/expenses-a4.component';
import { ExpensesK57Component } from 'src/app/cafe/components/print/expenses/K57/expenses-k57/expenses-k57.component';
import { ExpensesK80Component } from 'src/app/cafe/components/print/expenses/K80/expenses-k80/expenses-k80.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-expenses',
  templateUrl: './expenses.component.html',
  styleUrls: ['./expenses.component.scss']
})
export class ExpensesComponent implements OnInit {

  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  data: any;
  type_expense: any;
  radioValue: any;
  detailExpensesForm: FormGroup;
  money_currency: any;
  list_suppliers: any = [];
  listPaymentBank: any = [];
  expendGroup: any = []
  disabled = true;
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    public vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhAuth: VhAuth,
  ) {
    this.vhQueryCafe.refreshLocalSpending_groups().then(() => {
      this.expendGroup = this.vhQueryCafe.getlocalSpending_groups()
    })
    this.data = this.router.getCurrentNavigation().extras.state.invoice;
    this.type_expense = this.data.bill_type;
    this.detailExpensesForm = new FormGroup({
      date: new FormControl({ value: this.data.date, disabled: true }),
      customer: new FormControl({ value: this.data.bill_type == 12 || this.data.bill_type == 28 ? this.data.id_supplier : this.data.name, disabled: true }),
      total: new FormControl({ value: this.data.total, disabled: true }),
      content: new FormControl({ value: this.data.content, disabled: true }),
      payment_type: new FormControl({ value: this.data.payment_type, disabled: true }),
      wallet: new FormControl({ value: this.vhQueryCafe.getlocalWallet(this.data['id_wallet']), disabled: true }),
      type_receipt: new FormControl({ value: this.data.bill_type, disabled: true }),
      id_group: new FormControl({ value: this.data.bill_type == 8 || this.data.bill_type == 26 ? this.data.id_group : null, disabled: true })
    });
    this.radioValue = this.data.payment_type.toString();
  }

  ngOnInit(): void {
    this.list_suppliers = this.vhQueryCafe.getlocalSuppliers();
}

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }
  /**back */
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/cashflow/money-control'], {
      state: this.dataRestore.dataRestore
    });
  }
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.data['content'] = this.detailExpensesForm['value'].content
        this.data['supplier'] = [12, 28].includes(this.type_expense) ? this.vhQueryCafe.getlocalSupplier(this.data['id_supplier']) : this.detailExpensesForm['value'].customer
        this.data['employee'] = this.vhAuth.getlocalEmployee(this.data['id_employee'])
        this.data['total'] = parseInt(this.money_currency.getRawValue())
        this.vhComponent.showModal(this.renderPrintPage(printer['type']), {
          billInfo: this.data,
          printer: printer,
        }, false, false, `modal-print-${printer['type']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { })
          })
      }
    }, (err) => {
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/cafe/dashboard/management/printer']))
    })
  }
  private renderPrintPage(type: 'K57' | 'K80' | 'A4') {
    switch (type) {
      case 'K57': return ExpensesK57Component
      case 'K80': return ExpensesK80Component
      default: return ExpensesA4Component
    }
  }
  /*----------Print------------------*/
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getlocalPrinters().find(item => item.name == "DESKTOP")
      if (printer && printer.expense.enable) resolve(printer.expense)
      else rejects("No")
    })
  }
  checkBillType(bill_type) {
    //kim tra loi ha n
    switch (bill_type) {
      case 8: {
        return (this.languageService.translate("Other expenses"));
      }
      case 12: {
        return (this.languageService.translate("Pay for supplier liabilities"));
      }
    }
  }
}
