import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MoneyControlComponent } from './money-control.component'; 
import { TranslateModule } from '@ngx-translate/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { InputComponent } from './components/input/input.component'; 
import { ReturnSupplierComponent } from './components/return-supplier/return-supplier.component';
import { SalesComponent } from './components/sales/sales.component';
import { ExpensesComponent } from './components/expenses/expenses.component';
import { ReceiptsComponent } from './components/receipts/receipts.component';
import { TransferReceiveComponent } from './components/transfer-receive/transfer-receive.component';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';

const routes: Routes = [
  {
    path: "",
    component: MoneyControlComponent
  },
  {
    path: "sales",
    component: SalesComponent
  },
  {
    path: "supplier-return",
    component: ReturnSupplierComponent
  },
  {
    path: "input",
    component: InputComponent
  },
  {
    path: "expenses",
    component: ExpensesComponent
  },
  {
    path: "receipts",
    component: ReceiptsComponent
  },
  {
    path: "transfer-receive",
    component: TransferReceiveComponent
  },
]

@NgModule({
  declarations: [
    MoneyControlComponent,
    SalesComponent,
    ReturnSupplierComponent,
    InputComponent,
    ExpensesComponent,
    ReceiptsComponent,
    TransferReceiveComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    FormsModule,
    IonicModule,
    ReactiveFormsModule
  ]
})
export class MoneyControlModule { }
